@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.module {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  height: 100vh;
  margin: auto;
  text-align: center;
  padding: 4rem;
}
.module__image {
  height: auto;
  width: 100%;
}
.module__image img {
  object-fit: cover;
}
.module__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 2rem;
}
.module__text {
  margin-top: 2rem;
  line-height: 2.4rem;
  color: hsl(0deg, 0%, 7%);
}
.module__title {
  font-size: 3rem;
  color: hsl(0deg, 0%, 7%);
}
.module__box {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.module__button {
  margin: 0 0 10% 0;
  background-color: hsl(21deg, 50%, 79%);
  color: hsl(0deg, 0%, 100%);
  font-weight: 700;
  padding: 1rem 3rem;
  border-radius: 3.2rem;
  border: none;
}
.module__button:hover {
  cursor: pointer;
  background-color: rgb(240, 213, 200);
}